.checkbox .card {
  background-color: inherit;
  border-top: 0px;
  border-bottom: 0px; }

.checkbox .card-img-top {
  height: 40px;
  width: 40px; }

.checkbox .card-body {
  padding-bottom: 0px; }

.checkbox .card .card-title {
  font-family: Montserrat;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 1.47px; }

.checkbox .card .card-text {
  font-family: Montserrat;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 1.47px;
  color: #3e3d3d;
  text-align: center;
  font-size: 45px; }



